import WorkflowService from '@/services/commons/WorkflowService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let WorkflowTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultWorkflowTableFilter: {
      type: String,
      default: null
    },
    defaultWorkflowTableOrderBy: {
      type: String,
      default: null
    },
    ignoreWorkflowId: {
      type: String,
      default: null
    },
    workflowTypeId: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      workflowTableData: [],
      workflowTableFilterTimer: null,
      workflowTableFilter: this.defaultWorkflowTableFilter,
      workflowTableOrderBy: this.defaultWorkflowTableOrderBy,
      workflowTableContainer: 'loading-container',
      workflowTableBusy: false,
      workflowTableTotalRows: 0,
      workflowTableCurrentPage: 1,
      workflowTablePerPage: 10
    }
  },
  computed: {
    workflowTableNoDataText: function () {
      return this.workflowTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    workflowTableTotalPages: function () {
      return Math.ceil(this.workflowTableTotalRows / this.workflowTablePerPage)
    },
    workflowTablePagingInfo: function () {
      return this.pagingInfo(this.workflowTableCurrentPage, this.workflowTablePerPage, this.workflowTableTotalRows)
    }
  },
  methods: {
    loadWorkflowTableData (loadingContainerId) {
      let self = this
      this.workflowTableBusy = true
      this.workflowTableContainer = loadingContainerId || this.workflowTableContainer
      this.startLoading(this.workflowTableContainer)
      WorkflowService.workflows({
        pageIndex: this.workflowTableCurrentPage - 1,
        pageSize: this.workflowTablePerPage,
        filter: this.workflowTableFilter,
        orderByField: this.workflowTableOrderBy,
        ignoreWorkflowId: this.ignoreWorkflowId,
        workflowTypeId: this.workflowTypeId,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.workflowTableContainer)
        self.workflowTableBusy = false
        self.workflowTableTotalRows = response.data.totalRows
        self.workflowTableData = response.data.workflowList || []
      }).catch(function (error) {
        self.stopLoading(self.workflowTableContainer)
        self.workflowTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshWorkflowTable () {
      this.loadWorkflowTableData()
    },
    filterWorkflowTable (filter) {
      let self = this
      if (this.workflowTableFilter !== filter) {
        clearTimeout(this.workflowTableFilterTimer)
        this.workflowTableFilterTimer = setTimeout(() => {
          self.workflowTableFilter = filter
          self.loadWorkflowTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.workflowTableFilterTimer)
      }
    },
    orderWorkflowTable (orderBy, active) {
      this.workflowTableOrderBy = (active ? '' : '-') + orderBy
      this.loadWorkflowTableData()
    }
  },
  watch: {
    workflowTableCurrentPage: function () {
      this.refreshWorkflowTable()
    },
    workflowTablePerPage: function () {
      this.refreshWorkflowTable()
    },
    ignoreWorkflowId: function () {
      this.refreshWorkflowTable()
    },
    workflowTypeId: function () {
      this.refreshWorkflowTable()
    }
  }
}

export default WorkflowTableMixin
